body {
  margin: 0;
  font-family: -apple-system, Lora, Karla-Regular, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(255, 246, 220);
  color: rgb(0, 0, 0);
}

h1 {
  font-family: -apple-system, "Helvetica Neue", sans-serif;
}

h4 {
  font-family: -apple-system, Lora, Karla-Regular, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
}

h5 {
  font-family: -apple-system, Lora, Karla-Regular, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
}

p {
  font-family: -apple-system, Lora, Karla-Regular, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}

/* INDIVIDUAL COMPONENT STYLING */

.center-text {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

.body-text {
  max-width: 80%;
}

.title {
  display: flex;
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: left;
  margin: 15px;
}

.main-title {
  display: flex;
  justify-content: left;
  font-family: -apple-system, "helvetica", sans-serif;
  font-size: 9.57em;
  color: #be1600;
  line-height: 130px;
}

/* Individual sections */
.home-section {
  background-color: #e6dfd0;
  display: flex;
  /* justify-content: left; */
}

.section1 {
  background-color: #def8ec;

  /* background-image: url("https://www.publicdomainpictures.net/pictures/310000/velka/damask-vintage-background-wallpaper-1566400309WS6.jpg") ; */
}

.section0 {
  background-color: rgb(255, 246, 220);
}

.content-style {
  display: flex;
  flex-direction: column;
}

/* FLEXING */

.flex-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.flex-top {
  display: flex;
  flex-direction: column-reverse;
}

.flex-left {
  display: flex;
  justify-content: left;
  /* margin-left: 10px; */
}

.flex-right {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 45px;
}

.btn-clear {
  border: none;
  background: transparent;
}

.nav-link {
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

.image-scale {
  width: 100vw;
}

.direction {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btm-cntr {
  display: none;
  justify-content: column-reverse;
  flex-direction: "co;lumn";
  justify-content: "flex-end";
  align-items: "center";
  padding-right: "6vw";
}

.scroll {
  display: "inline";
  padding: "5px";
}

.image-scale {
  width: 55vw;
}

/* IMAGE TOGGLE CLASSES */

.active {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 100;
  transition: opacity 0.3s;
}

.inactive {
  /* background-color: red; */
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  transition: opacity 0.3s;
}

.flex-child {
  position: relative;
}

@keyframes bounce {
  from {
    transform: translateY(20%);
  }

  to {
    transform: translateY(-10%);
  }
}

@media screen and (max-width: 850px) {
  .main-title {
    display: flex;
    justify-content: left;
    font-family: -apple-system, "Helvetica Neue", sans-serif;
    font-size: 12vw;
    color: #dc2c21;
    line-height: 0.95em;
    width: 80%;
  }

  .flex-center {
    margin-left: calc(18% - 28px);
  }

  .subtitle {
    font-size: 4vw;
    width: 50vw;
    margin-left: 5px;
  }

  .title {
    width: 80%;
  }

  .postcard {
    width: 70vw;
  }

  .image-scale {
    width: 70vw;
  }
  .body-text {
    font-size: 12px;
  }

  .fp-tableCell {
    display: flex;
    align-items: center;
  }
}

/*Animation Station*/

#down-arrows {
  animation-duration: 1s;
  animation-name: bounce;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  rotate: 50;
}

